<template>
  <v-form ref="loginForm">
    <v-text-field
      v-model="username"
      append-icon="fas fa-user"
      background-color="white"
      label="Никнейм"
      placeholder="Введите никнейм"
      outlined
      required
    />

    <v-text-field
      v-model="password"
      append-icon="fas fa-lock"
      background-color="white"
      label="Пароль"
      placeholder="Введите пароль"
      outlined
      type="password"
      required
    />

    <v-text-field
      v-model="repeatPassword"
      append-icon="fas fa-lock"
      background-color="white"
      label="Повторите пароль"
      placeholder="Повторите пароль"
      outlined
      type="password"
      required
    />

    <v-btn
      color="primary"
      class="mb-4"
      block
      @click="submit"
    >
      Зарегистрироваться
    </v-btn>
  </v-form>
</template>

<script>
export default {
  name: 'AppRegisterForm',
  props: {
    value: {
      type: Object,
      default() {
        return {
          username: '',
          password: '',
          repeatPassword: '',
        };
      },
    },
  },
  data() {
    return {
      username: this.value.username,
      password: this.value.password,
      repeatPassword: this.value.repeatPassword,
    };
  },
  watch: {
    value(val) {
      const { username, password, repeatPassword } = val;

      this.username = username;
      this.password = password;
      this.repeatPassword = repeatPassword;
    },
  },
  methods: {
    submit() {
      this.$emit('input', {
        username: this.username,
        password: this.password,
        repeatPassword: this.repeatPassword,
      });
    },
  },
};
</script>

