<template>
  <v-container fluid>
    <v-row
      align="center"
      justify="center">
      <v-col
        cols="12"
        sm="8"
        md="6"
        lg="4">
        <AppRegisterForm :value="userRegisterFormData" @input="onInput"/>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import AppRegisterForm from '@/components/AppRegisterForm';
import { A_REGISTER } from '@/store/modules/user/action-types';

export default {
  name: 'RegisterPage',
  components: { AppRegisterForm },
  data() {
    return {
      userRegisterFormData: {
        username: '',
        password: '',
        repeatPassword: '',
      },
    };
  },
  methods: {
    onInput(value) {
      this.userRegisterFormData = value;
      this.$store.dispatch(`user/${A_REGISTER}`, { ...value });
    },
  },
};
</script>

